
import React,{Component} from 'react';
import FormModal from '../../components/hd-formModal'
import { message,Button  } from 'antd';
import { CodePreviewer, MdPreviewer, PreviewLayout } from "react-code-previewer";
import installMd from '../md/hd-formModal/intro.md'
import apiMd from '../md/hd-formModal/api.md'
import example from '../md/hd-formModal/example.md'

const popUpFormConfig = {
  layout:'horizontal', //horizontal,vertical,inline
  formLayout:{
    labelCol: { xs: { span: 24 }, sm: { span: 5 } },
    wrapperCol: { xs: { span: 22 }, sm: { span: 18 } }
  },
  forms:[
    {
    key: "input",
    label: "",
    type: "input",
    disabled:true,
    rules:[{required:true}]
  }, {
    key: "inputNumber",
    label: "",
    type: "inputNumber",
    width:300
  },{
    key: "select",
    label: "",
    type: "select",
    children:[{label: "",value: "1"},
      {label: "",value: "2"}]
  }, 
  {
    key: "datePicker",
    label: "",
    type: "datePicker",
    format:"YYYY-MM-DD HH:mm:ss"
  },{
    key: "rangePickerTime",
    label: "2",
    type: "rangePickerTime",
    format:"YYYY-MM-DD HH:mm:ss",
    placeholder:['','']
  },
  {
    key: "simName",
    label: "",
    type: "upload",
  }, {
    key: "checkbox",
    label: "",
    type: "checkbox",
    initialValue:'Apple1',
    children:[{ label: 'Apple', value: 'Apple1' },
    { label: 'Pear', value: 'Pear1' },
    { label: 'Orange', value: 'Orange1' }]
  },{
    label: "",
    type: "radio",
    key: "radio",
    initialValue:'Apple2',
    children:[{ label: 'Apple', value: 'Apple2' },
    { label: 'Pear', value: 'Pear2' },
    { label: 'Orange', value: 'Orange2' },]
  },{
    key: "textarea",
    label: "",
    type: "textarea",
    rows:3,
    layout:{
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 5,
        },
      },
    }
  }]
}
class HdSearchFormPage extends Component{
    state = {
      visible:false,
      title:""
    }
    // 
    handleCreate = ()=>{
      const form = this.formRef.props.form;
      form.validateFields((err, values) => {
        console.log(values)
        if (err) {
          return;
        }
      })
    }
    handleCancel = ()=>{
      this.setState({
        visible:false
      })
    }

    handleAdd = ()=>{
      this.setState({
        visible:true,
        record:null
      })
    }
    handleEdit = ()=>{
      this.setState({
        visible:true,
        title:"",
        record:{input:'',inputNumber:'7878787',textarea:'66666',datePicker:'2018-12-12 12:54:34',checkbox:['Apple1','Pear1'],radio:"Pear2",select:"2"},
       })
    }
    render(){
    
        return <div>
          <FormModal
              wrappedComponentRef={form => (this.formRef = form)}
              visible={this.state.visible}
              onCancel={this.handleCancel}
              onCreate={this.handleCreate}
              record={this.state.record}
              formConfig={popUpFormConfig}
              isEdit={this.state.isEdit}
              title = {this.state.title}
              destroyOnClose = "true"
              width="600px"
              okText=""
              cancelText=""
              
        />
        <PreviewLayout>
          <MdPreviewer md={installMd}></MdPreviewer>
          <CodePreviewer code={example} showCode={true}>
              <Button type="primary" onClick = {this.handleAdd}></Button>
              <Button type="danger"  onClick = {this.handleEdit} style={{"marginLeft":'20px'}}></Button>
          </CodePreviewer>
          <MdPreviewer md={apiMd}></MdPreviewer>
        </PreviewLayout>
      </div>
    }
}
export default HdSearchFormPage